@import url(https://fonts.googleapis.com/css?family=Akaya+Telivigala&display=swap);
@import url(https://fonts.googleapis.com/css?family=Akaya+Telivigala&display=swap);
@import url(https://fonts.googleapis.com/css?family=Akaya+Telivigala&display=swap);
@import url(https://fonts.googleapis.com/css?family=Akaya+Telivigala&display=swap);
html,
body {
  
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    max-width: 100%;
    overflow-x:hidden;
}
@media screen and (max-width: 1440px) {
    body {
        max-width: 100%;
        overflow-x:hidden;
    }
  }
.About {
  /* display: flex; */
  justify-content: space-evenly;
}


.about {
  display: grid;
  grid-template-columns: 900px 420px;
  grid-gap: 50px;
  gap: 50px;
}

.aboutText {
  color: rgb(183, 195, 197);
  font-size: 1.5em;
  margin-top: 120px;
  margin-left: 150px;
}

@media screen and (max-width: 425px) {
  .aboutText {
    font-size: 1.45em;
    width: 520px;
    margin-left: 100px;
  }
}

@media screen and (max-width: 425px) {
  .about {
    display: block;
    font-size: 2.2em;
    margin-left: 100px;
  }
}

@media screen and (max-width: 425px) {
  .image {
    margin-left: 120px;
  }
}

@media screen and (max-width: 425px) {
  ul.skillList {
    display: grid;
    grid-template-columns: none;
  }
}

body {
  background-color: rgb(8, 10, 24);
}

.aboutText a {
  color: white;
}

.exp-text h4 {
  color: rgb(146, 92, 22);
}

.time {
  position: relative;
  left: 20;
}

.time p {
  color: white;
  font-size: 0.8em;
  font-weight: bolder;
}



h2 {
  font-size: clamp(40px, 8vw, 80px);
  color: rgb(183, 195, 197);
}

button {
  background-color: transparent;
  border: 1px solid;
  border-radius: 4px;
  color: rgb(64, 154, 102);
  height: 40px;
  width: 170px;
  font-size: 0.6em;
}

button:hover {
  cursor: pointer;
  background-color: rgba(123, 175, 145, 0.233);
}

.contactSect {
  margin-right: 920px;
}

.contactIcons {
  position: fixed;
  top: 450px;
  color: rgb(2, 75, 53);
}

.contactIcons :hover {
  cursor: pointer;
  color: rgb(241, 241, 241);
}

@media screen and (min-width: 768px) {
  ul.skillList {
    display: grid;
    grid-template-columns: 290px 540px;
    grid-gap: 20px;
    gap: 20px;
    font-size: smaller;
  }
}
.skillList li {
  color: rgb(64, 154, 102);
}

.image {
  margin-top: 150px;
  width: 420px;
}

.about h4 {
  color: rgb(23, 177, 177);
}

.overallExp {
  display: grid;
  grid-template-columns: 1000px 800px;
}

.openSource{
  margin-top: -40px;
}

.experience {
  display: inline-block;
  color: white;
  min-height: 100vh;
  width: 100%;
}

@media screen and (min-width: 320px) {
  .experience {
    width: 1000px;
    margin-left: 20px;
  }
}

@media screen and (min-width: 768px) {
  .experience {
    width: 1200px;
    margin-left: 50px;
  }
}

.exp-text {
  color: white;
  margin: 180px;
}

@media screen and (max-width: 320px) {
  .exp-text {
    font-size: 1.7em;
    margin-left: 120px;
    width: 320px;
  }
}

.expList li {
  list-style-type: square;
  padding: 15px;
}

.exp-text h1 {
  color: rgb(23, 177, 177);
  text-align: center;
}

.exp-text h4 {
  color: rgb(9, 150, 79);
  font-size: 1.8em;
}

@media screen and (max-width: 1440px) {
  .overallExp {
   display: block;
  }
}
.projects {
  display: inline-block;
  font-size: 1.7em;
  color: white;
  width: 100%;
  height: 100vh;
}

.indivProject {
  display: grid;
  grid-template-columns: 900px 120px;
}

.projImage img {
  width: 450px;
  height: 450px;
  margin-top: 150px;
}

@media screen and (min-width: 320px) {
  .projects {
    width: 100%;
    margin-left: 60px;
    height: 100%;
  }
}



.project-text {
  color: white;
  font-size: 0.8em;
  margin-left: 180px;
  margin-top: 240px;
}

.tech {
  margin-left: 250px;
  font-size: 0.7em;
  color: rgb(9, 150, 79);
}
@media screen and (min-width: 320px) {
  .project-text {
    margin-left: 80px;
    font-size: 1.7em;
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .project-text {
    text-align: center;
  }
}



@media screen and (min-width: 1024px) {
  .indivProject {
    font-size: 0.4em;
    display: grid;
    grid-template-columns: 499px 120px;
    grid-gap: 180px;
  }
}

@media screen and (min-width: 768px) {
  .tech {
    margin-left: 0px;
  }
}

@media screen and (max-width: 320px) {
  .projImage img {
    display: none;
  }
}
a {
  text-decoration: none;
}

a:hover {
  color: saddlebrown;
}

h4 {
  color: rgb(15, 89, 102);
  text-align: center;
  font-size: 1.2em;
}

.contact {
  width: 100%;
  height: 100vh;
}
@media screen and (max-width: 320px) {
  .contact {
    width: 100%;
    margin-left: 300px;
    height: 100%;
    text-align: center;
  }
}
.contact-text {
  color: white;
  margin-top: 750px;
  font-size: 2em;
  width: 700px;
  margin-left: 450px;
}

.contact-text h1 {
  color: rgb(15, 89, 102);
}

@media screen and (max-width: 320px) {
  .contact-text {
    margin-left: 80px;
    font-size: 1.7em;
    width: 170px;
  }
}

.contactIcons {
  padding: 40px;
  cursor: pointer;
}

@media screen and (min-width: 320px) {
  .contactIcons {
    width: 150px;
    margin-left: -10px;
    height: 1000px;
  }
}

svg {
  margin: 20px;
}

.contact button {
  margin-left: 200px;
}

@media screen and (max-width: 320px) {
  .contact button {
    margin-left: 0px;
    font-size: larger;
  }
}

ul.Headerlist {
    list-style-type: none;
    margin-left: 700px;
    width: 600px;
  }

 li{
    display: inline;
    padding: 8px;
    cursor: pointer;
    margin: 10px;
}  

a {
    color: rgb(251, 251, 251);
}


.NavBar a:hover {
    color: rgb(11, 143, 50);
  }

div.NavBar{
    height: 50px;
    position: fixed;
    top: 5;
    width: 100%;
    background-color: rgb(3, 28, 53);
    z-index: 1;
}  

@media screen and (min-width: 320px) {
    ul.Headerlist{
        list-style-type: none;
        margin-left: 100px;
        width: 600px;
    }
}

.intro {
    font-size: 1.7em;
    min-height: 100vh;
    width: 100%;
    padding: 40px;
  }
  
  .intro p {
    color: white;
  }
  
  @media screen and (max-width: 425px) {
    .intro {
        display: block;
        font-size: 4.2em;
        width: 620px;
        margin-left: 100px;
    }
  }

  @media screen and (max-width: 425px) {
    button {
        width: 450px;
        height: 120px;
    }
  }

  
  @media screen and (min-width: 1024px) {
    .intro {
      width: 800px;
      margin-left: 100px;
    }
  }
  
  #intro-text {
    display: inline;
    margin-top: 10px;
    text-align: left;
    width: 900px;
    margin-left: 160px;
  }

  #intro-text h3 {
    color: rgb(23, 177, 177);
    font-size: clamp(40px, 8vw, 80px);
  }
